ul{list-style-type: none; margin:0; padding: 0;}
a{text-decoration: none; }
img{display: block; border:0px none;}
#header_section{
    height:44px;
    background-color:#fff;
    position:fixed;
    left:0;
    right:0;
    top:0;
    z-index:99999;
    border-bottom:1px solid #ccc;
    #nav{
        max-width:980px;
        margin:0 auto;
        text-align:center;
        ul{
            display:flex;
            justify-content: center;
            align-items: center;
            gap:10px;
            a{
                display:block; height: 40px; line-height:40px;
                padding:0 24px; font-size:14px; letter-spacing: 0.1;
                font-weight: bold;
                color:darkblue;
                &:hover{
                    color:#f60; text-decoration: underline;
                }
            }
        }
    }
}

#main_content{
    .main_visual{
        width:100%; height: 774px;  
        .inner{
            max-width:1920px; width:100%; height:100%; margin:0 auto;
            img{
                width:100%; height: 100%; object-fit: cover;
            }
        }
        
    }
    .title{
    font-size:18px; border-bottom: 1px solid #d9d9d9;
    margin-bottom:10px; padding-bottom:10px;
    }
    #contents{
        width: 100%; max-width: 980px; margin:0 auto;
        .now_movies{
            margin:40px 0 60px; 
            .movies_wrap{
                padding-top:20px; display:flex;
                flex-flow:row wrap; justify-content: space-between;
                gap:15px;
                li{
                    flex: 1 1 0;
                    img{
                        width: 100%; height: auto; object-fit:cover;
                    }
                }
            }
        }
    }
    .special{
        margin-bottom:34px;
        .sp_wrap{
            display:flex; justify-content:space-between; padding:16px 0px;
            /*flex-flow:row wrap overflow:hidden; */
        }
    }
    .evt{
        margin-bottom:34px;
        .evt_wrap{
            height: 512px; /*display:flex; flex-flow:row wrap;
            gap:11px; /*position: relative;*/
            display:grid; grid-template-rows:repeat(2,1fr);
                grid-template-columns:repeat(6, 200px);
                grid-template-areas:
                 " a b c c d d "
                 " e e e f d d "
            ;

            li{
                height:250px;
            }
            .e1{width: 183px; grid-area:a; }
            .e2{width: 183px; grid-area:b;}
            .e3{width: 286px; grid-area:c;}
            .e4{width: 295px; height: 511px; /*position:absolute; right: 0; top: 0;*/grid-area:d; } 
            .e5{width: 478px; grid-area:e;}
            .e6{width: 183px; grid-area:f;}
        }
    }
    .direct_btns{
        display:flex; justify-content: space-between;
    }   
}   
#evt_box{
    padding-top:100px; display:flex; justify-content:center;
}